'use strict';

/**
* Chi-square distributed pseudorandom numbers.
*
* @module @stdlib/random-base-chisquare
*
* @example
* import chisquare from '@stdlib/random-base-chisquare' ;
*
* var v = chisquare( 6 );
* // returns <number>
*
* @example
* import { factory as factory } from '@stdlib/random-base-chisquare' ;
*
* var chisquare = factory( 3, {
*     'seed': 297
* });
*
* var v = chisquare();
* // returns <number>
*/

// MODULES //

import setReadOnly from '@stdlib/utils-define-nonenumerable-read-only-property' ;
import main from './main.js' ;
import factory from './factory.js' ;


// MAIN //

setReadOnly( main, 'factory', factory );


// EXPORTS //

export default main;
